.collectableContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 15px;
    border-radius: 10px;
    border: 3px solid black;
    background-color: rgba(255, 255, 255, .9);
    box-shadow: 2px 2px 0px black;
    gap: 20px;
    width: calc(50% - 46px);
    height: inherit;
    justify-content: space-between;

    &.checked {
        opacity: .6;
    }

    .image {
        height: 40px;
    }

    .textContainer {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        gap: 10px;
        width: calc(100% - 130px);
        
        .beschreibung {
            line-height: 25px;
        }
    
        .ort {
    
        }
    }

    input {
        display: none;
    }

    .input {
        width: 24px;
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: white;
        border: 3px solid black;
        border-radius: 5px;
        cursor: pointer;

        .inputCheckmark {
            font-size: 18px;
            font-weight: bold;
        }
    }

    @media (max-width: 780px) {
        width: 100%;
    }
}